import './App.css';
import { useState, useEffect } from 'react';
import { Modal, Button, Nav } from 'react-bootstrap';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import Container from 'react-bootstrap/Container';
import { BrowserRouter as Router, Routes, Route, Link, Navigate, useNavigate } from 'react-router-dom';
import NavDropdown from 'react-bootstrap/NavDropdown';
import FriendList from './FriendList';
import axios from 'axios';

import LoginModal from './LoginModal';
import logo from './images/logo.png';
import Notice from './pages/Notice.js';
import AllBoard from './pages/AllBoard.js';
import BoardInsert from './pages/BoardInsert.js';
import MemberRegist from './pages/MemberRegist.js';
import FindIdPw from './pages/FindIdPw.js';
import MemberInfo from './pages/MemberInfo.js';
import Service from './pages/Service.js';
import PointShop from './pages/PointShop.js';
import Sample from './pages/Sample.js';
import BoardDetail from './pages/BoardDetail.js';
import BoardUpdate from './pages/BoardUpdate.js';
import ChangeEmail from './pages/ChangeEmail.js';
import ChangePw from './pages/ChangePW.js';
import ChangeEmoji from './pages/ChangeEmoji.js';

function App() {
  const [modalShow, setModalShow] = useState(false);
  const [loggedIn, setLoggedIn] = useState(false);
  const [username, setUsername] = useState('');
  const navigate = useNavigate();

  useEffect(() => {
    const token = localStorage.getItem('token');
    if (token) {
      setLoggedIn(true);
      const savedUsername = localStorage.getItem('username');
      if (savedUsername) {
        setUsername(savedUsername);
      }
    }
  }, []);

  const handleLoginSuccess = (username, token) => {
    localStorage.setItem('token', token);
    localStorage.setItem('username', username);
    setLoggedIn(true);
    setUsername(username);
    setModalShow(false);
  };

  const handleLogout = () => {
    localStorage.removeItem('token');
    localStorage.removeItem('username');
    setLoggedIn(false);
    setUsername('');
    navigate('/', { replace: true });
  };

  return (
    <div className="App">
      <section className="con menu">
        <div className="icon">
          <div className="img-box">
            <a href="/"><img src={logo} width={130} /></a>
          </div>
        </div>
        <div className="right">
          <nav>
            <ul>
              {loggedIn ? (
                <li>
                  환영합니다, <Link to="/memberInfo">{username}</Link>님 &nbsp;
                  <Button variant="secondary" onClick={handleLogout}>
                    로그아웃
                  </Button>
                </li>
              ) : (
                <li>
                  <Button variant="primary" onClick={() => setModalShow(true)}>
                    로그인
                  </Button>{' '}
                  <a href="/memberRegist">회원가입</a>
                </li>
              )}
              <LoginModal
                show={modalShow}
                onHide={() => setModalShow(false)}
                handleLoginSuccess={handleLoginSuccess}
              />
            </ul>
          </nav>
        </div>
      </section>
      <section className="con main-menu">
        <div className="main-menu-2">
          <nav>
            <ul>
              <li>
                <NavDropdown title="게시판 목록" id="basic-nav-dropdown">
                  <NavDropdown.Item as={Link} to="/allBoard/0">자유 게시판</NavDropdown.Item>
                  <NavDropdown.Item as={Link} to="/allBoard/1">리버스 1999</NavDropdown.Item>
                  <NavDropdown.Item as={Link} to="/allBoard/2">붕괴 스타레일</NavDropdown.Item>
                  <NavDropdown.Item as={Link} to="/allBoard/3">작혼</NavDropdown.Item>
                  <NavDropdown.Item as={Link} to="/allBoard/4">NIKKE</NavDropdown.Item>
                  <NavDropdown.Item as={Link} to="/allBoard/5">마작 일번가</NavDropdown.Item>
                </NavDropdown>
              </li>
            </ul>
          </nav>
        </div>
      </section>
      <div className="main-ui">
        <div id="Mains-left">
          
        </div>
        <Routes>
          <Route path="/" element={
            <>
              <div id='Mains-center'>
                <div className="grid-board">
                  <Container>
                    <Row>
                      <Col><BoardList categoryId={0} boardTitle="자유 게시판" /></Col>
                      <Col><BoardList categoryId={1} boardTitle="리버스 1999" /></Col>
                    </Row>
                  </Container>
                  <Container>
                    <Row>
                      <Col><BoardList categoryId={2} boardTitle="붕괴 스타레일" /></Col>
                      <Col><BoardList categoryId={3} boardTitle="작혼" /></Col>
                    </Row>
                  </Container>
                  <Container>
                    <Row>
                      <Col><BoardList categoryId={4} boardTitle="NIKKE" /></Col>
                      <Col><BoardList categoryId={5} boardTitle="마작 일번가" /></Col>
                    </Row>
                  </Container>
                </div>
              </div>
            </>
          } />
          <Route path="/notice" element={<Notice />} />
          <Route path="/allBoard/:categoryId" element={<AllBoard />} />
          <Route path="/boardInsert" element={<PrivateRoute loggedIn={loggedIn}><BoardInsert /></PrivateRoute>} />
          <Route path="/memberRegist" element={<MemberRegist />} />
          <Route path="/findIdPw" element={<FindIdPw />} />
          <Route path="/memberInfo" element={<PrivateRoute loggedIn={loggedIn}><MemberInfo /></PrivateRoute>} />
          <Route path="/service" element={<Service />} />
          <Route path="/pointShop" element={<PointShop />} />
          <Route path="/sample" element={<Sample />} />
          <Route path="/detail" element={<BoardDetail />} />
          <Route path="/boardUpdate" element={<BoardUpdate />} />
          <Route path="/changeEmail" element={<ChangeEmail />} />
          <Route path="/changePw" element={<ChangePw />} />
          <Route path="/changeEmoji" element={<ChangeEmoji />} />
          <Route path="*" element={<div><h2>404 ERROR</h2> (아직 만들지 않았거나 없는 페이지에요!)</div>} />
        </Routes>
        <div id="Mains-right">
          
          {loggedIn && <FriendList />}
        </div>
      </div>
    </div>
  );
}


function PrivateRoute({ children, loggedIn }) {
  useEffect(() => {
    if (!loggedIn) {
      alert('로그인을 해주세요!');
    }
  }, [loggedIn]);

  return loggedIn ? children : <Navigate to="/" replace />;
}

function BoardList({ categoryId, boardTitle }) {
  const [posts, setPosts] = useState([]);

  useEffect(() => {
    const fetchPosts = async () => {
      try {
        const response = await axios.get('http://192.168.0.144:8080/ex01/board/list', {
          params: {
            categoryId,
            pageNum: 1,
            pageSize: 4
          }
        });
        if (response.data && response.data.boardList) {
          setPosts(response.data.boardList);
        }
      } catch (error) {
        console.error('게시물 가져오기 중 오류 발생:', error);
      }
    };

    fetchPosts();
  }, [categoryId]);

  return (
    <div className="container-wrapper">
      <div className="container mx-auto px-4 py-6">
        <div className="board">
          <div className="board-header">
            <h1 className="board-title">{boardTitle}</h1>
            <button className="board-btn">
              <Link to={`/allBoard/${categoryId}`}>+ 더 보기</Link>
            </button>
          </div>
          <div className="board-content">
            <ul className="post-list">
              {posts.map((post) => (
                <li key={post.boardId}>
                  <Link to={`/detail?boardId=${post.boardId}`} className="post-link">
                    {post.boardTitle}
                  </Link>
                </li>
              ))}
            </ul>
          </div>
        </div>
      </div>
    </div>
  );
}

export default App;
