import React, { useState, useEffect } from 'react';
import Button from 'react-bootstrap/Button';
import Modal from 'react-bootstrap/Modal';
import Form from 'react-bootstrap/Form';
import { useNavigate } from 'react-router-dom';
import axiosInstance from '../axiosConfig';

function MemberInfo() {
    const [memberInfo, setMemberInfo] = useState(null);
    const [loading, setLoading] = useState(true);
    const [error, setError] = useState(null);
    const [showPasswordModal, setShowPasswordModal] = useState(false);
    const [showDeleteConfirmModal, setShowDeleteConfirmModal] = useState(false);
    const [selectedAction, setSelectedAction] = useState('');
    const [isAuthenticated, setIsAuthenticated] = useState(false);
    const navigate = useNavigate();

    const memberId = localStorage.getItem('username');

    useEffect(() => {
        const fetchMemberInfo = async () => {
            try {
                const token = localStorage.getItem('token');
                const response = await axiosInstance.get(`/ex01/member/detail/${memberId}`, {
                    headers: {
                        'Authorization': `Bearer ${token}`
                    }
                });
                setMemberInfo(response.data);
            } catch (error) {
                setError(error);
            } finally {
                setLoading(false);
            }
        };

        fetchMemberInfo();
    }, [memberId]);

    const handlePasswordModalShow = (action) => {
        setSelectedAction(action);
        setShowPasswordModal(true);
    };

    const handlePasswordModalHide = () => {
        setShowPasswordModal(false);
    };

    const handleDeleteConfirmModalHide = () => {
        setShowDeleteConfirmModal(false);
    };

    const deleteMember = async () => {
        try {
            const token = localStorage.getItem('token');
            await axiosInstance.post('/ex01/member/delete', { memberId }, {
                headers: {
                    'Authorization': `Bearer ${token}`
                }
            });
            alert('회원 탈퇴 성공. 감사합니다.');
            localStorage.removeItem('username');
            localStorage.removeItem('token');
            navigate('/'); // 탈퇴 후 로그인 페이지로 이동
            
         window.location.reload(); // 페이지를 새로고침하여 상태를 업데이트
        } catch (error) {
            setError('회원 탈퇴 중 오류가 발생했습니다.');
            console.log('회원 탈퇴 중 오류가 발생했습니다.');
        }
    };

    if (loading) {
        return <div>불러오는 중입니다...</div>;
    }

    if (error) {
        return <div>오류: {error.message}</div>;
    }

    if (!memberInfo) {
        return <div>회원 정보를 찾을 수 없습니다.</div>;
    }

    return (
        <div className="member-container">
            <div className="member-card">
                <div className="member-header">
                    <h1>회원 페이지</h1>
                </div>
                <div className="member-profile">
                    <div className="member-avatar">
                        <svg className="avatar-icon" fill="currentColor" viewBox="0 0 24 24">
                            <path d="M12 12c2.21 0 4-1.79 4-4s-1.79-4-4-4-4 1.79-4 4 1.79 4 4 4zm0 2c-2.67 0-8 1.34-8 4v2h16v-2c0-2.66-5.33-4-8-4z" />
                        </svg>
                    </div>
                    <div className="member-profile-info">
                        <div className="member-profile-name">{memberInfo.memberId}</div>
                    </div>
                    <div className="member-profile-actions">
                        <Button className="member-button" onClick={() => handlePasswordModalShow('email')}>이메일 변경</Button>
                        <Button className="member-button" onClick={() => handlePasswordModalShow('password')}>비밀번호 변경</Button>
                        <Button className="member-button" onClick={() => handlePasswordModalShow('delete')}>회원 탈퇴</Button>
                    </div>
                </div>
            </div>
            <TabContent memberInfo={memberInfo} />
            <PasswordModal
                show={showPasswordModal}
                onHide={handlePasswordModalHide}
                selectedAction={selectedAction}
                memberId={memberId}
                setIsAuthenticated={setIsAuthenticated}
                setShowDeleteConfirmModal={setShowDeleteConfirmModal} // 추가된 부분
            />
            <DeleteConfirmModal
                show={showDeleteConfirmModal}
                onHide={handleDeleteConfirmModalHide}
                onConfirm={deleteMember}
            />
        </div>
    );
}

function TabContent({ memberInfo }) {
    const formatDate = (dateString) => {
        const date = new Date(dateString);
        const year = date.getFullYear();
        const month = String(date.getMonth() + 1).padStart(2, '0');
        const day = String(date.getDate()).padStart(2, '0');
        return `${year}-${month}-${day}`;
    };

    return (
        <div className="member-container">
            <div className="member-card">
                <div className="member-tab-content">
                    <table className="member-info-table">
                        <tbody>
                            <tr>
                                <th>아이디</th>
                                <td>{memberInfo.memberId}</td>
                            </tr>
                            <tr>
                                <th>이메일 주소</th>
                                <td>{memberInfo.memberEmail}</td>
                            </tr>
                            <tr>
                                <th>가입일</th>
                                <td>{formatDate(memberInfo.memberRegistDate)}</td>
                            </tr>
                        </tbody>
                    </table>
                </div>
            </div>
        </div>
    );
}

function PasswordModal({ show, onHide, selectedAction, memberId, setIsAuthenticated, setShowDeleteConfirmModal }) { // 수정된 부분
    const [password, setPassword] = useState('');
    const [error, setError] = useState('');
    const navigate = useNavigate(); // 추가된 부분

    const handlePasswordChange = (event) => {
        setPassword(event.target.value);
    };

    const handlePasswordSubmit = async () => {
        try {
            const response = await axiosInstance.post('/ex01/member/check', {
                memberId,
                memberPassword: password
            });
            console.log('Response data:', response.data);
            if (response.data === 1) {
                alert('인증 성공');
                setIsAuthenticated(true);
                onHide();
                if (selectedAction === 'delete') {
                    setShowDeleteConfirmModal(true);
                } else {
                    switch (selectedAction) {
                        case 'email':
                            navigate('/changeEmail');
                            break;
                        case 'password':
                            navigate('/changePw');
                            break;
                        default:
                            break;
                    }
                }
            } else {
                setError('비밀번호가 올바르지 않습니다.');
            }
        } catch (error) {
            setError('서버 오류가 발생했습니다.');
        }
    };

    useEffect(() => {
        if (show) {
            setPassword('');
            setError('');
        }
    }, [show]);

    return (
        <Modal show={show} onHide={onHide} centered>
            <Modal.Header closeButton>
                <Modal.Title>비밀번호 확인</Modal.Title>
            </Modal.Header>
            <Modal.Body>
                <Form>
                    <Form.Group className="mb-3">
                        <Form.Label>비밀번호</Form.Label>
                        <Form.Control
                            type="password"
                            placeholder="비밀번호를 입력하세요"
                            value={password}
                            onChange={handlePasswordChange}
                        />
                    </Form.Group>
                    {error && <p style={{ color: 'red' }}>{error}</p>}
                </Form>
            </Modal.Body>
            <Modal.Footer>
                <Button variant="secondary" onClick={onHide}>취소</Button>
                <Button variant="primary" onClick={handlePasswordSubmit}>확인</Button>
            </Modal.Footer>
        </Modal>
    );
}

function DeleteConfirmModal({ show, onHide, onConfirm }) {
    return (
        <Modal show={show} onHide={onHide} centered>
            <Modal.Header closeButton>
                <Modal.Title>회원 탈퇴 확인</Modal.Title>
            </Modal.Header>
            <Modal.Body>정말로 탈퇴하시겠습니까?</Modal.Body>
            <Modal.Footer>
                <Button variant="secondary" onClick={onHide}>취소</Button>
                <Button variant="danger" onClick={onConfirm}>탈퇴</Button>
            </Modal.Footer>
        </Modal>
    );
}

export default MemberInfo;
