import React, { useEffect, useState } from 'react';
import Button from 'react-bootstrap/Button';
import axiosInstance from '../axiosConfig';
import { useNavigate } from 'react-router-dom';

const ChangePW = () => {
    const [formData, setFormData] = useState({
        memberPassword: '',
        passwordConfirm: '',
    });
    const memberId = localStorage.getItem('username');
    const navigate = useNavigate();

    const [validationMessages, setValidationMessages] = useState({
        pwMsg: '',
        pwConfirmMsg: '',
    });

    const [flags, setFlags] = useState({
        pwFlag: false,
        pwConfirmFlag: false,
    });

    const handleBlur = async (e) => {
        const { id, value } = e.target;

        switch (id) {
            case 'memberPassword':
                validatePassword(value);
                break;
            case 'passwordConfirm':
                validatePasswordConfirm(value);
                break;
            default:
                break;
        }
    };

    const handleChange = (e) => {
        const { id, value } = e.target;
        setFormData((prev) => ({
            ...prev,
            [id]: value,
        }));
    };

    const validatePassword = (password) => {
        const pwRegExp = /^(?=.*?[A-Z])(?=.*?[a-z])(?=.*?[0-9])(?=.*?[!@#$%^&*]).{8,16}$/;
        if (password === '') {
            setValidationMessages((prev) => ({
                ...prev,
                pwMsg: '비밀번호는 필수에오. 필수!',
            }));
            setFlags((prev) => ({ ...prev, pwFlag: false }));
            return;
        }
        if (!pwRegExp.test(password)) {
            setValidationMessages((prev) => ({
                ...prev,
                pwMsg: '비밀번호는 소문자, 대문자, 숫자, 특수문자(!@#$%^&*) 중 최소 하나씩을 포함한 8~16 자리여야 합니다.',
            }));
            setFlags((prev) => ({ ...prev, pwFlag: false }));
        } else {
            setValidationMessages((prev) => ({
                ...prev,
                pwMsg: '사용 가능한 비밀번호임니다.',
            }));
            setFlags((prev) => ({ ...prev, pwFlag: true }));
        }
    };

    const validatePasswordConfirm = (pwConfirm) => {
        if (pwConfirm === '') {
            setValidationMessages((prev) => ({
                ...prev,
                pwConfirmMsg: '비밀번호 확인은 필수에오.',
            }));
            setFlags((prev) => ({ ...prev, pwConfirmFlag: false }));
            return;
        }
        if (formData.memberPassword === pwConfirm) {
            setValidationMessages((prev) => ({
                ...prev,
                pwConfirmMsg: '비밀번호 확인을 통과했습니다.',
            }));
            setFlags((prev) => ({ ...prev, pwConfirmFlag: true }));
        } else {
            setValidationMessages((prev) => ({
                ...prev,
                pwConfirmMsg: '저런! 비밀번호를 다시 한번확인해 주세요.',
            }));
            setFlags((prev) => ({ ...prev, pwConfirmFlag: false }));
        }
    };

    const handleJoin = async () => {
        const { pwFlag, pwConfirmFlag } = flags;
        if (pwFlag && pwConfirmFlag) {
            try {
                const response = await axiosInstance.post('/ex01/util/modifyPw', {
                    memberId,
                    memberPassword: formData.memberPassword
                });
                if (response.status === 200) {
                    alert('비밀번호가 수정되었습니다.');
                    window.location.href = '/';
                } else {
                    alert('수정에 실패하였습니다. 다시 시도해주세요.');
                }
            } catch (error) {
                console.error(error);
                alert('수정에 실패했습니다. 다시 시도해주세요.');
            }
        } else {
            alert('모든 필드를 올바르게 작성해 주세요.');
        }
    };

    const handleBack = () => {
        navigate(-1);
    };

    return (
        <div className="container mx-auto px-4 py-8">
            <div className="board-name">
                <strong><h4>비밀번호 변경</h4></strong>
            </div>
            <div className="bg-white shadow-md rounded-lg p-6">
                <div>
                    <label className="label">비밀번호</label>
                    <span>
                        <input
                            id="memberPassword"
                            className="input"
                            type="password"
                            name="memberPassword"
                            title="비밀번호"
                            maxLength="20"
                            value={formData.memberPassword}
                            onChange={handleChange}
                            onBlur={handleBlur}
                        />
                        <br />
                    </span>
                    <span id="pwMsg" style={{ color: flags.pwFlag ? 'green' : 'red' }}>
                        {validationMessages.pwMsg}
                    </span>

                    <label className="label">비밀번호 확인</label>
                    <span>
                        <input
                            id="passwordConfirm"
                            className="input"
                            type="password"
                            name="passwordConfirm"
                            title="비밀번호 확인"
                            maxLength="20"
                            value={formData.passwordConfirm}
                            onChange={handleChange}
                            onBlur={handleBlur}
                        />
                        <br />
                    </span>
                    <span id="pwConfirmMsg" style={{ color: flags.pwConfirmFlag ? 'green' : 'red' }}>
                        {validationMessages.pwConfirmMsg}
                    </span>
                </div>
                <Button id="btnJoin" variant="outline-primary" onClick={handleJoin}>
                    비밀번호 변경
                </Button>
                <Button variant="secondary" onClick={handleBack} style={{ marginLeft: '10px' }}>
                    뒤로가기
                </Button>
            </div>
        </div>
    );
};

export default ChangePW;
