import React, { useState, useEffect } from "react";
import { Link, useParams } from 'react-router-dom';
import Button from 'react-bootstrap/Button';
import axiosInstance from "../axiosConfig";
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import Pagination from 'react-bootstrap/Pagination';
import Form from 'react-bootstrap/Form';
import Modal from 'react-bootstrap/Modal';

const AllBoard = () => {
  const { categoryId } = useParams();
  const [boardList, setBoardList] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  const [currentPage, setCurrentPage] = useState(0);
  const [totalPages, setTotalPages] = useState(0);
  const [searchOption, setSearchOption] = useState('title');
  const [searchKey, setSearchKey] = useState('');
  const [showModal, setShowModal] = useState(false);
  const [selectedMemberId, setSelectedMemberId] = useState('');
  const loggedIn = Boolean(localStorage.getItem('token'));
  const currentUser = localStorage.getItem('username');

  const pageSize = 5;

  const getBoardList = async (page = 0) => {
    try {
      const params = {
        pageNum: page + 1,
        pageSize: pageSize,
        categoryId: parseInt(categoryId, 10) || 0
      };
      const resp = await axiosInstance.get(`/ex01/board/list`, { params });
      if (resp && resp.data && resp.data.boardList && resp.data.pageMaker) {
        const { boardList, pageMaker } = resp.data;
        setBoardList(boardList);
        const totalItems = pageMaker.totalCount;
        const totalPages = Math.ceil(totalItems / pageSize);
        setTotalPages(totalPages);
        setCurrentPage(page);
      } else {
        setBoardList([]);
      }
    } catch (error) {
      setError(error);
      setBoardList([]);
    } finally {
      setLoading(false);
    }
  };

  const searchBoardList = async (page = 0) => {
    try {
      const params = {
        searchOption: searchOption,
        search: searchKey,
        pageNum: page + 1,
        pageSize: pageSize,
        categoryId: parseInt(categoryId, 10) || 0
      };
      const resp = await axiosInstance.get(`/ex01/board/search`, { params });
      if (resp && resp.data && resp.data.boardList && resp.data.pageMaker) {
        const { boardList, pageMaker } = resp.data;
        setBoardList(boardList);
        const totalItems = pageMaker.totalCount;
        const totalPages = Math.ceil(totalItems / pageSize);
        setTotalPages(totalPages);
        setCurrentPage(page);
      } else {
        setBoardList([]);
        setTotalPages(0);
      }
    } catch (error) {
      setError(error);
      setBoardList([]);
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    if (searchKey) {
      searchBoardList(currentPage);
    } else {
      getBoardList(currentPage);
    }
  }, [currentPage, categoryId, searchKey, searchOption]);

  const handlePageChange = (page) => {
    setLoading(true);
    setCurrentPage(page);
  };

  const handleSearch = (e) => {
    e.preventDefault();
    setCurrentPage(0);
    if (searchKey) {
      searchBoardList(0);
    } else {
      getBoardList(0);
    }
  };

  const handleAddFriendClick = (memberId) => {
    setSelectedMemberId(memberId);
    setShowModal(true);
  };

  const handleAddFriendRequest = async () => {
    try {
      const response = await axiosInstance.post('/ex01/friend/send', {
        memberId: localStorage.getItem('username'),
        receiverId: selectedMemberId
      });
      if (response.data === 1) {
        alert('친구 요청을 보냈습니다.');
      } else {
        alert('친구 요청에 실패했습니다.');
      }
    } catch (error) {
      console.error('친구 요청 오류:', error);
      alert('친구 요청 처리 중 오류가 발생했습니다.');
    } finally {
      setShowModal(false);
    }
  };

  const renderPagination = () => {
    const items = [];
    const groupSize = 5;
    const currentGroup = Math.floor(currentPage / groupSize);
    const startPage = currentGroup * groupSize;
    const endPage = Math.min(startPage + groupSize, totalPages);

    items.push(
      <Pagination.First key="first" onClick={() => handlePageChange(0)} disabled={currentPage === 0} />
    );
    items.push(
      <Pagination.Prev key="prev" onClick={() => handlePageChange(Math.max(startPage - groupSize, 0))} disabled={currentGroup === 0} />
    );

    for (let page = startPage; page < endPage; page++) {
      items.push(
        <Pagination.Item key={page} active={page === currentPage} disabled={page === currentPage} onClick={() => handlePageChange(page)}>
          {page + 1}
        </Pagination.Item>
      );
    }

    items.push(
      <Pagination.Next key="next" onClick={() => handlePageChange(endPage)} disabled={endPage >= totalPages} />
    );
    items.push(
      <Pagination.Last key="last" onClick={() => handlePageChange(totalPages - 1)} disabled={currentPage === totalPages - 1} />
    );

    return items;
  };

  const getBoardTitle = (categoryId) => {
    switch (parseInt(categoryId, 10)) {
      case 0:
        return '자유 게시판';
      case 1:
        return '리버스 1999';
      case 2:
        return '붕괴 스타레일';
      case 3:
        return '작혼 : 리치마작';
      case 4:
        return '승리의 여신 : 니케';
      case 5:
        return '마작 일번가';

      default:
        return '자유 게시판';
    }
  };

  if (loading) {
    return <div>불러오는 중입니다...</div>;
  }

  if (error) {
    return <div>오류: {error.message}</div>;
  }

  return (
    <div className="container mx-auto px-4 py-8">
      <div className="board-name">
        <strong><h4>{getBoardTitle(categoryId)}</h4></strong>
      </div>
      <div className="board-insert">
        <Button variant="primary" size="sm">
          <Link to={`/boardInsert?categoryId=${categoryId}`} style={{ color: 'white', textDecoration: 'none' }}>글작성</Link>
        </Button>
      </div>
      <div className="bg-white shadow-md rounded-lg p-6">
        <Container className="custom-dashed pb-4">
          <Row className="grid grid-cols-4 gap-4">
            <Col className="board-num"><strong>번호</strong></Col>
            <Col xs={5}><strong>제목</strong></Col>
            <Col className="board-member"><strong>작성자</strong></Col>
            <Col className="board-date"><strong>작성 날짜</strong></Col>
          </Row>
        </Container>
        <Container className="custom-dashed py-4">
          {Array.isArray(boardList) && boardList.length > 0 ? (
            boardList.map((board) => (
              <Row key={board.boardId} className={`grid grid-cols-4 gap-4 ${board !== boardList.length - 1 ? 'board-row' : ''}`}>
                <Col className="board-num2">{board.boardId}</Col>
                <Col>
                  <Link to={`/detail?boardId=${board.boardId}`} style={{ textDecoration: 'none', color: 'black' }}>
                    {board.boardTitle}
                  </Link>
                </Col>
                <Col className="board-member" onClick={() => loggedIn && currentUser !== board.memberId ? handleAddFriendClick(board.memberId) : null} style={{ cursor: loggedIn && currentUser !== board.memberId ? 'pointer' : 'default' }}>
                  {board.memberId}
                </Col>
                <Col className="board-date">{formatDate(board.boardRegistDate)}</Col>
              </Row>
            ))
          ) : (
            <div>게시글이 없습니다.</div>
          )}
        </Container>
        <br />
        <Form className="d-flex justify-content-end mb-3" onSubmit={handleSearch}>
          <Form.Control as="select" value={searchOption} onChange={(e) => setSearchOption(e.target.value)} className="mr-2" style={{ width: '20%' }}>
            <option value="title">제목</option>
            <option value="content">내용</option>
          </Form.Control>
          <Form.Control
            type="text"
            placeholder="검색어를 입력하세요"
            value={searchKey}
            onChange={(e) => setSearchKey(e.target.value)}
            className="mr-2"
            style={{ width: '50%' }}
          />
          <Button type="submit" variant="primary">검색</Button>
        </Form>
        <div className="d-flex justify-content-center">
          <Pagination>{renderPagination()}</Pagination>
        </div>
      </div>

      <Modal show={showModal} onHide={() => setShowModal(false)}>
        <Modal.Header closeButton>
          <Modal.Title>친구 추가</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <p>{selectedMemberId}님에게 친구 요청을 보내시겠습니까?</p>
        </Modal.Body>
        <Modal.Footer>
          <Button variant="secondary" onClick={() => setShowModal(false)}>
            취소
          </Button>
          <Button variant="primary" onClick={handleAddFriendRequest}>
            요청 보내기
          </Button>
        </Modal.Footer>
      </Modal>
    </div>
  );
};

export default AllBoard;

const formatDate = (dateString) => {
  const date = new Date(dateString);
  const year = date.getFullYear();
  const month = String(date.getMonth() + 1).padStart(2, '0');
  const day = String(date.getDate()).padStart(2, '0');
  return `${year}-${month}-${day}`;
};
