import React, { useState, useEffect } from 'react';
import Button from 'react-bootstrap/Button';
import { useNavigate, useLocation } from 'react-router-dom';
import axiosInstance from '../axiosConfig';

const BackPage = () => {
    const navigate = useNavigate();
    const onClickBtn = () => {
        navigate(-1);
    };
    return (
        <Button variant="secondary" onClick={onClickBtn}>취소</Button>
    );
};

const BoardUpdate = () => {
    const [board, setBoard] = useState({
        boardTitle: '',
        memberId: '',
        boardContent: '',
        boardRegistDate: new Date().toISOString().split('T')[0], // YYYY-MM-DD 형식으로 초기화
        attachVO: [] // attachVO를 빈 배열로 초기화
    });

    const [files, setFiles] = useState([]);
    const [error, setError] = useState('');
    const [fileLimitExceeded, setFileLimitExceeded] = useState(false);
    const localStorageMemberId = localStorage.getItem('username');
    const navigate = useNavigate();
    const location = useLocation();

    useEffect(() => {
        const fetchBoard = async () => {
            try {
                const queryParams = new URLSearchParams(location.search);
                const boardId = queryParams.get('boardId');
                const response = await axiosInstance.get(`/ex01/board/detail?boardId=${boardId}`);
                const { boardTitle, memberId, boardContent, attachVO, boardRegistDate } = response.data;
                const formattedBoardRegistDate = typeof boardRegistDate === 'string'
                    ? boardRegistDate.split('T')[0]
                    : new Date(boardRegistDate).toISOString().split('T')[0]; // YYYY-MM-DD 형식으로 변환
                setBoard({ 
                    boardTitle, 
                    memberId, 
                    boardContent, 
                    boardRegistDate: formattedBoardRegistDate, 
                    attachVO: attachVO || [] 
                });
            } catch (error) {
                console.error('Error fetching board:', error);
            }
        };

        fetchBoard();
    }, [location.search]);

    const onChange = (event) => {
        const { value, name } = event.target;
        setBoard({
            ...board,
            [name]: value,
        });
    };

    const onFileChange = async (event) => {
        const selectedFiles = Array.from(event.target.files);

        if (selectedFiles.length > 3) {
            setError('첨부파일의 최대 갯수는 3개입니다.');
            setFileLimitExceeded(true);
            return;
        }

        setFileLimitExceeded(false);
        setFiles(selectedFiles); // 새 파일로 교체

        const formData = new FormData();
        for (let i = 0; i < selectedFiles.length; i++) {
            formData.append('file', selectedFiles[i]);
        }

        try {
            const response = await axiosInstance.post('/ex01/board/attach', formData, {
                headers: {
                    'Content-Type': 'multipart/form-data',
                },
            });
            console.log('파일 등록 서버 응답:', response);
            if (!Array.isArray(response.data)) {
                console.error("응답이 배열 형태가 아닙니다:", response.data);
                return;
            }
            // 새 파일들의 attachVO 설정
            const newAttachVO = response.data.map((item) => ({
                attachPath: item.attachPath,
                attachRealName: item.attachRealName,
                attachChgName: item.attachChgName,
                attachExtension: item.attachExtension,
                attachId: item.attachId // attachId 추가
            }));
            // 기존 파일을 제거하고 새 파일로 교체
            setBoard((prevBoard) => ({
                ...prevBoard,
                attachVO: newAttachVO
            }));
            alert('파일 등록 성공!');
        } catch (error) {
            console.error('파일 등록 오류:', error);
            setError('파일 등록 중 오류가 발생했습니다.');
        }
    };

    const updateBoard = async () => {
        setError('');

        if (!board.boardTitle.trim() || !localStorageMemberId.trim() || !board.boardContent.trim()) {
            setError('제목, 작성자, 내용은 필수 입력 사항입니다.');
            return;
        }

        const queryParams = new URLSearchParams(location.search);
        const boardId = queryParams.get('boardId');

        const boardData = {
            boardId: boardId,
            boardTitle: board.boardTitle,
            memberId: localStorageMemberId,
            boardContent: board.boardContent,
            boardRegistDate: board.boardRegistDate,
            attachVO: board.attachVO
        };

        try {
            const response = await axiosInstance.post(`/ex01/board/update`, boardData, {
                headers: {
                    'Content-Type': 'application/json',
                },
            });
            console.log('서버 응답:', response);
            alert('게시글 수정 성공!');
            navigate(-1);
        } catch (error) {
            console.error('오류:', error);
            if (error.response) {
                console.error('응답 데이터:', error.response.data);
                console.error('응답 상태:', error.response.status);
                console.error('응답 헤더:', error.response.headers);
            }
            setError('글 수정 중 오류가 발생했습니다.');
        }
    };

    const deleteFiles = () => {
        setBoard((prevBoard) => ({
            ...prevBoard,
            attachVO: [] // attachVO를 빈 배열로 설정
        }));
        setFiles([]);
        setError('');
        setFileLimitExceeded(false);
        document.getElementById('fileInput').value = null;
    };

    const { boardTitle, boardContent } = board;

    return (
        <div className="container mx-auto px-4 py-8">
            <div>
                <div className="board-name">
                    <strong><h4>게시글 수정</h4></strong>
                </div>
                <div className="bg-white shadow-md rounded-lg p-6">
                    <div className="Write">
                        <input
                            type='text'
                            id='title_txt'
                            name="boardTitle"
                            value={boardTitle}
                            onChange={onChange}
                            required
                        />
                    </div>
                    <div>
                        <input
                            type="text"
                            value={localStorageMemberId}
                            disabled // memberId 입력 필드 비활성화
                        />
                    </div>
                    <div>
                        <textarea
                            id='content_txt'
                            name="boardContent"
                            value={boardContent}
                            onChange={onChange}
                            required
                        />
                    </div>
                </div>
                <div>
                    <p>파일 업로드 :</p>
                    <input type="file" id="fileInput" name="file" onChange={onFileChange} multiple />
                </div>
                {error && <p style={{ color: 'red' }}>{error}</p>}
                <div className="Insert-Button">
                    <Button variant="outline-danger" onClick={deleteFiles}>파일 삭제</Button>
                    {' '}
                    <Button variant="outline-primary" onClick={updateBoard} disabled={fileLimitExceeded}>수정</Button>
                    {' '}
                    <BackPage />
                </div>
            </div>
        </div>
    );
};

export default BoardUpdate;
