import React, { useEffect, useState } from 'react';
import Button from 'react-bootstrap/Button';
import axios from 'axios';
import { Link } from 'react-router-dom';

const ChangeEmoji = () => {
    const memberId = localStorage.getItem('username');

    const [emojis, setEmojis] = useState([]);
    const [error, setError] = useState(null);
    const [deleteList, setDeleteList] = useState([]);

    useEffect(() => {
        axios.get(`http://192.168.0.144:8080/ex01/member/detail?memberId=${memberId}`)
            .then(response => {
                const memberPropertyAsString = response.data.memberPropertyAsString;
                console.log('memberPropertyAsString:', memberPropertyAsString);

                try {
                    const emojisArray = JSON.parse(memberPropertyAsString);
                    console.log('Parsed emojisArray:', emojisArray);
                    setEmojis(emojisArray);
                } catch (error) {
                    console.error('Failed to parse memberPropertyAsString:', error);
                    setError('Failed to parse member properties.');
                }
            })
            .catch(error => {
                console.error('Failed to fetch data:', error);
                setError('Failed to fetch data.');
            });
    }, [memberId]);

    const handleCheckboxChange = (emojiIndex) => {
        setDeleteList((prevDeleteList) => {
            const isChecked = !prevDeleteList.includes(emojiIndex);
            const updatedDeleteList = isChecked
                ? [...prevDeleteList, emojiIndex]
                : prevDeleteList.filter(index => index !== emojiIndex);

            console.log(`Emoji ${isChecked ? 'selected' : 'deselected'}:`, emojis[emojiIndex]);
            return updatedDeleteList;
        });
    };

    const handleDeleteSelected = () => {
        const isConfirmed = window.confirm('정말 삭제하시겠습니까?');
        if (isConfirmed) {
            const deleteItems = deleteList.map(index => emojis[index]); // 이모티콘 목록

            axios.post('http://192.168.0.144:8080/ex01/member/deleteProperty', {
                memberId: memberId,
                memberProperty: deleteItems
            }, {
                headers: {
                    'Content-Type': 'application/json'
                }
            })
            .then(response => {
                if (response.data === 1) {
                    alert('삭제되었습니다.');
                    const remainingEmojis = emojis.filter((_, index) => !deleteList.includes(index));
                    setEmojis(remainingEmojis);
                    setDeleteList([]);
                } else {
                    alert('이모지 삭제에 문제가 생겼습니다.');
                }
            })
            .catch(error => {
                console.error('Failed to delete properties:', error);
                alert('서버 오류가 발생했습니다.');
            });
        }
    };

    const hasEmojis = Array.isArray(emojis) && emojis.length > 0;

    return (
        <div className="container mx-auto px-4 py-8">
            <div className="board-name">
                <strong><h4>이모티콘 보유 목록</h4></strong>
            </div>
            <div className="bg-white shadow-md rounded-lg p-6">
                {error && <div className="text-red-500">{error}</div>}
                <div className="row">
                    {hasEmojis ? (
                        <>
                            {emojis.map((emoji, index) => (
                                <EmojiCard
                                    key={index}
                                    emoji={emoji}
                                    index={index}
                                    isSelected={deleteList.includes(index)}
                                    onCheckboxChange={handleCheckboxChange}
                                />
                            ))}
                            <p>더 많은 이모티콘을 원하시나요?</p>
                        </>
                    ) : (
                        <div>
                            <p>보유중인 이모티콘이 없습니다. 이모티콘을 구매해주세요</p>
                            <Button variant="primary"><Link to='/pointShop'>
                                이모티콘 상점으로 이동</Link>
                            </Button>
                        </div>
                    )}
                </div>
                {hasEmojis && (
                    <Button variant="danger" onClick={handleDeleteSelected} disabled={deleteList.length === 0}>
                        선택된 이모티콘 삭제
                    </Button>
                )}
            </div>
        </div>
    );
};

function EmojiCard({ emoji, index, isSelected, onCheckboxChange }) {
    return (
        <div className="col-md-4 mb-4">
            <div className="card h-100">
                <img src={emoji.imageUrl} alt={emoji.title} className="card-img-top" />
                <div className="card-body">
                    <h5 className="card-title">{emoji.title}</h5>
                    <p className="card-text">{emoji.content}</p>
                    <div className="form-check">
                        <input
                            type="checkbox"
                            className="form-check-input"
                            id={`checkbox-${index}`}
                            checked={isSelected}
                            onChange={() => onCheckboxChange(index)}
                        />
                        <label className="form-check-label" htmlFor={`checkbox-${index}`}>
                            선택
                        </label>
                    </div>
                </div>
            </div>
        </div>
    );
}

export default ChangeEmoji;
