import React, { useState, useEffect } from 'react';
import Button from 'react-bootstrap/Button';
import { useNavigate, useLocation } from 'react-router-dom';
import axiosInstance from '../axiosConfig';

const BackPage = () => {
    const navigate = useNavigate();
    const onClickBtn = () => {
        navigate(-1);
    };
    return (
        <Button variant="secondary" onClick={onClickBtn}>취소</Button>
    );
};

const BoardInsert = () => {
    const [board, setBoard] = useState({
        boardTitle: '',
        boardContent: '',
        categoryId: 0,
        attachVO: [] // attachVO를 빈 배열로 초기화
    });

    const [files, setFiles] = useState([]);
    const [error, setError] = useState('');
    const [fileLimitExceeded, setFileLimitExceeded] = useState(false);
    const location = useLocation();
    const navigate = useNavigate();

    useEffect(() => {
        const queryParams = new URLSearchParams(location.search);
        const categoryId = queryParams.get('categoryId');
        setBoard((prevBoard) => ({
            ...prevBoard,
            categoryId: parseInt(categoryId, 10) || 0
        }));
    }, [location]);

    const onChange = (event) => {
        const { value, name } = event.target;
        setBoard({
            ...board,
            [name]: value,
        });
    };

    const onFileChange = async (event) => {
        const selectedFiles = Array.from(event.target.files);
        const totalFiles = selectedFiles.length + files.length;

        if (totalFiles > 3) {
            setError('첨부파일의 최대 갯수는 3개입니다.');
            setFileLimitExceeded(true);
            return;
        }

        setFileLimitExceeded(false);
        setFiles([...files, ...selectedFiles]);

        const formData = new FormData();
        for (let i = 0; i < selectedFiles.length; i++) {
            formData.append('file', selectedFiles[i]);
        }

        try {
            const response = await axiosInstance.post('/ex01/board/attach', formData, {
                headers: {
                    'Content-Type': 'multipart/form-data'
                }
            });
            console.log('파일 등록 서버 응답:', response);
            if (!Array.isArray(response.data)) {
                console.error("응답이 배열 형태가 아닙니다:", response.data);
                return;
            }
            // attachVO 설정
            const newAttachVO = response.data.map((item) => ({
                attachPath: item.attachPath,
                attachRealName: item.attachRealName,
                attachChgName: item.attachChgName,
                attachExtension: item.attachExtension
            }));
            setBoard((prevBoard) => ({
                ...prevBoard,
                attachVO: [...prevBoard.attachVO, ...newAttachVO]
            }));
            alert('파일 등록 성공!');
        } catch (error) {
            console.error('파일 등록 오류:', error);
            setError('파일 등록 중 오류가 발생했습니다.');
        }
    };

    const saveBoard = async () => {
        setError('');

        if (!board.boardTitle.trim() || !board.boardContent.trim()) {
            setError('제목, 내용은 필수 입력 사항입니다.');
            return;
        }

        const formData = new FormData();
        formData.append('categoryId', board.categoryId);
        formData.append('boardTitle', board.boardTitle);
        formData.append('memberId', localStorage.getItem('username'));
        formData.append('boardContent', board.boardContent);
        if (board.attachVO.length > 0) {
            board.attachVO.forEach((item, index) => {
                formData.append(`attachVO[${index}].attachPath`, item.attachPath);
                formData.append(`attachVO[${index}].attachRealName`, item.attachRealName);
                formData.append(`attachVO[${index}].attachChgName`, item.attachChgName);
                formData.append(`attachVO[${index}].attachExtension`, item.attachExtension);
            });
        }

        try {
            const response = await axiosInstance.post('/ex01/board/regist', formData, {
                headers: {
                    'Content-Type': 'application/json'
                }
            });
            console.log('서버 응답:', response);
            alert('게시글 등록 성공!');
            navigate(-1);
        } catch (error) {
            console.error('오류:', error);
            if (error.response) {
                console.error('응답 데이터:', error.response.data);
                console.error('응답 상태:', error.response.status);
                console.error('응답 헤더:', error.response.headers);
            }
            setError('글 작성 중 오류가 발생했습니다.');
        }
    };

    const deleteFiles = () => {
        setBoard((prevBoard) => ({
            ...prevBoard,
            attachVO: [] // attachVO를 빈 배열로 설정
        }));
        setFiles([]);
        setError('');
        setFileLimitExceeded(false);
        document.getElementById('fileInput').value = null;
    };

    return (
        <div className="container mx-auto px-4 py-8">
            <div>
                <div className="board-name">
                    <strong><h4>게시글 작성</h4></strong>
                </div>
                <div className="bg-white shadow-md rounded-lg p-6">
                    <div className="Write">
                        <input
                            type='text'
                            id='title_txt'
                            name="boardTitle"
                            value={board.boardTitle}
                            placeholder="제목"
                            onChange={onChange}
                            required
                        />
                    </div>
                    <div>
                        <textarea
                            id='content_txt'
                            placeholder='내용을 입력하세요.'
                            name="boardContent"
                            value={board.boardContent}
                            onChange={onChange}
                            required
                        />
                    </div>
                </div>
                <div>
                    <p>파일 업로드 :</p>
                    <input type="file" id="fileInput" name="file" onChange={onFileChange} multiple />
                </div>
                {error && <p style={{ color: 'red' }}>{error}</p>}
                <div className="Insert-Button">
                    <Button variant="outline-danger" onClick={deleteFiles}>파일 삭제</Button>
                    {' '}
                    <Button variant="outline-primary" onClick={saveBoard} disabled={fileLimitExceeded}>글 작성</Button>
                    {' '}
                    <BackPage />
                </div>
            </div>
        </div>
    );
};

export default BoardInsert;
