import React, { useState } from 'react';
import axiosInstance from './axiosConfig'; // axiosInstance 임포트
import { Link, useNavigate } from 'react-router-dom';
import { Modal, Button, Form } from 'react-bootstrap';

function LoginModal(props) {
  const [memberId, setMemberId] = useState('');
  const [password, setPassword] = useState('');
  const [error, setError] = useState('');
  const navigate = useNavigate();

  const handleMemberIdChange = (event) => {
    setMemberId(event.target.value);
  };

  const handlePasswordChange = (event) => {
    setPassword(event.target.value);
  };

  const handleLogin = async () => {
    if (!memberId || !password) {
      setError('아이디와 비밀번호를 모두 입력해주세요.');
      return;
    }

    const loginData = {
      memberId: memberId,
      memberPassword: password
    };

    console.log('로그인 요청 데이터:', loginData); // 디버깅용 로그

    try {
      const response = await axiosInstance.post('/ex01/login/check', loginData);
      const token = response.data; // JWT 토큰
      console.log('로그인 성공, 토큰:', token);
      
      // JWT 토큰을 로컬 스토리지에 저장
      localStorage.setItem('token', token);
      
      // 사용자 이름도 로컬 스토리지에 저장
      localStorage.setItem('username', memberId);

      props.handleLoginSuccess(memberId, token);
      navigate('/');
    } catch (error) {
      console.error('로그인 중 오류 발생:', error);
      setError('아이디 또는 비밀번호를 확인해주세요.');
    }
  };

  return (
    <Modal show={props.show} onHide={props.onHide} centered>
      <Modal.Header closeButton>
        <Modal.Title>로그인</Modal.Title>
      </Modal.Header>
      <Modal.Body>
        <Form>
          <Form.Group controlId="formBasicEmail">
            <Form.Label>아이디</Form.Label>
            <Form.Control 
              type="text" 
              placeholder="아이디를 입력하세요" 
              value={memberId}
              onChange={handleMemberIdChange}
            />
          </Form.Group>
          <br/>
          <Form.Group controlId="formBasicPassword">
            <Form.Label>비밀번호</Form.Label>
            <Form.Control 
              type="password" 
              placeholder="비밀번호를 입력하세요" 
              value={password}
              onChange={handlePasswordChange}
            />
          </Form.Group>
          {error && <p style={{ color: 'red' }}>{error}</p>}
        </Form>
        <br/>
        <Link to="/findIdPw">아이디/비밀번호 찾기</Link>
      </Modal.Body>
      <Modal.Footer>
        <Button variant="secondary" onClick={props.onHide}>
          닫기
        </Button>
        <Button variant="primary" onClick={handleLogin}>
          로그인
        </Button>
      </Modal.Footer>
    </Modal>
  );
}

export default LoginModal;
